header {
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
  width: max-content;
  margin: 0 auto;
}
header a {
  text-decoration: none;
  color: #000;
  text-align: center;
}

header h2 {
  color: #fff;
  background-color: #ff0000;
  padding: 8px 16px;
}

@media screen and (min-width: 600px) {
  header {
    margin: 0;
  }
}
